.poad {
	.hero__content {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: flex-start;
		background-color: rgba(0,0,0,0.6);
		max-width: 100%;
		color: #fff;
		img {
			max-height: calc(100vh - 200px);
			object-fit: contain; } }
	&__results-wrap {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center; } }

@media all and (max-width: 720px) {
	.poad {
		.hero__content {} } }
