.big-picture {
	display: flex;
	width: 100%;
	max-width: 100%;
	height: calc(100% - 80px);
	position: fixed;
	top: 60px;
	left: 0;
	background-color: rgba(0,0,0,0.5);
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 300ms linear;
	z-index: 100;
	img {
		max-width: 100%;
		object-fit: contain; } }
