.epic {
	.hero__content {
		display: flex;
		align-items: center;
		flex-direction: column;
		// justify-content: space-around
		background-color: rgba(0,0,0,0.6);
		max-width: 1440px;
		color: #fff;
		height: calc(100vh - 80px);
		div.switch-wrapper > div,
		div.switch-wrapper {
			height: 100%;
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start; }
		h1 {
			font-size: 32px;

			padding: 20px; }
		h2 {
			margin: 0 40px 40px 40px;
			font-weight: 400; }
		img {
			max-width: 100%;
			max-height: 100%;
			object-fit: contain;
			transform-origin: center center; } } }
.epic__results-wrap {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	.column {
		display: flex;
		flex-direction: column; }
	.column-left {
		align-items: center;
		width: 30%;
		min-width: 360px;
		.rc-calendar {
			margin-bottom: 5vh; }
		.buttons {
			margin: 10px 0;
			width: 60%;
			display: flex;
			flex-direction: row;
			justify-content: center; }
		.rotate {
			width: 50px; } }
	.column-right {
		width: 70%;
		p {
			padding: 10px 0;
			height: 60px; } } }
@media all and(max-width: 1000px) {
	html, body {
		overflow: auto; }
	.epic .hero__content {
		height: auto; }
	.epic .hero__content h1 {
		font-size: 24px;
		margin: 0;
		text-align: center;
		padding: 8px 0; }
	.epic .hero__content h2 {
		margin: 0 15px 5px 15px;
		font-size: 18px; }
	.hero.fullscreen-bg.epic {
		max-height: auto;
		overflow: auto; }
	.epic__results-wrap {
		display: block;
		flex: 1 1 0;
		flex-direction: column-reverse;
		justify-content: flex-end;
		height: auto;
		min-height: auto;
		width: 100%;
		padding-top: 20px;
		.column {
			display: flex;
			width: 100%;
			height: auto;
			padding: 0;
			&.shrunk {
				height: 80px; }
			.image-wrapper img:hover {
				transfrom: scale(1); } }
		.column-left {
			align-items: center;
			min-width: 100%;
			margin-botom: 20px;
			.rc-calendar {
				margin-bottom: 5vh; } }
		.column-right {} }
	.epic {
		.rc-calendar.calendar {
			margin-bottom: 10px; } } }
