@import '../../view_styles/variables.sass';
$header-height: 100px;
.poad-details {
	width: 100%;
	max-width: 100%;
	min-height: calc(100vh - 100px);
	height: auto;
	overflow: initial;
	overflow-x: hidden;
	position: absolute;
	top: 100px;
	left: 0;
	background-color: rgba(0,0,0,0.9);
	transition: all 300ms linear;
	z-index: 100;
	p {
		font: {
			size: 22px; }
		padding: 0 10% 40px 10%;
		&.small {
			margin: 30px 0 0 0;
			width: 100%;
			text-align: center; } }
	h3.heading {
		padding: 10px 0;
		margin: 0 0 20px 0;
		width: 100%;
		padding: 10px 10%;
		font: {
			size: 26px; }
		background-color: $light-purple; } }

@media all and(max-width: 1000px) {
	$header-height: 80px;
	.poad-details {
		p {
			font-size: 16px; } } }
@media all and (max-width: 720px) {
	.poad-details {
		height: calc(100vh - 80px);
		top: 80px; } }
@media all and (max-width: 640px) {
	.poad-details {
		height: calc(100vh - 60px);
		top: 60px; } }
