.hero__content {
	background-color: rgba(0,0,0,0.3); }

.cards {
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	max-width: 1440px;
	justify-content: space-around;
	align-items: center;
	flex: 1 0;
	a {
		display: flex;
		text-decoration: none;
		width: 40%;
		height: 40%; } }

@media all and (max-width: 640px) {
	.cards {
		max-width: 100%;
		flex-direction: column;
		padding: 2%;
		a {
			width: 80%;
			height: 25%; }
		.card__title {
			max-height: 125px;
			padding: 25px 0;
			text-align: center;
			font-size: 16px; } } }
